export class Vec2 {
	constructor(x=0,y=0) {
		this.x = x;
		this.y = y;
	}
}

export class Vec3 {
	constructor(x=0,y=0,z=0) {
		this.x = x;
		this.y = y;
		this.z = z;
	}
}

export class Vec4 {
	constructor(x=0,y=0,z=0,w=0) {
		this.x = x;
		this.y = y;
		this.z = z;
		this.w = w;
	}
}


